<template>
    <div>
        <!--Row>
            <search-form :tips="'请输入机种名称'"></search-form>
        </Row-->
        <div v-if="selected_model === ''">
            <Row>
                <Col span="12" offset="6">
                <select-form :tips="'请选择或输入机种名称'" :selectOption="selected_model"
                             @select-changed="selectChanged"
                             :optionList="optionList"></select-form>
                </Col>
            </Row>
        </div>
        <div v-else>
            <Row>
                <Col span="12" offset="6">
                <select-form :tips="'请选择或输入机种名称'" :selectOption="selected_model"
                             @select-changed="selectChanged"
                             :optionList="optionList"></select-form>
                </Col>
            </Row>
            <Row>
                <model-table style="margin-top: 10px"></model-table>
            </Row>
            <Row>
                <page-table></page-table>
            </Row>
            <Row>
                <Col span="8" offset="8">
                <model-form
                        ref="formRef"
                        v-model="modelFormData"
                        :formItems="newModelForm"
                        :formDataVaildate="formDataVaildate"
                        @on-click-submit="formSubmit">
                </model-form>
                </Col>
            </Row>
        </div>
    </div>
</template>

<script>
    import modelTable from '../common/table/table-list.vue';
    import searchForm from '../common/search/input-search.vue';
    import selectForm from '../common/form/select-form.vue';
    import pageTable  from '../common/pagination/page-table.vue';
    import modelForm from '../common/form/best-form.vue';
    import newModelForm from '../config/create_model.js';
    import util  from '../../libs/util.js';
    import parser from '../../libs/parser.js';
    import viewData from '../config/viewData.js';

    export default{
        components: {
            modelTable,
            searchForm,
            selectForm,
            pageTable,
            modelForm
        },
        data() {
            return {
                newModelForm: [],
                modelFormData: {},
                selected_model: '',
                show: false,
//                formDataVaildate:{
//                    model_name:[
//                        { required: true, message: 'Please enter model name', trigger: 'change' }
//                    ],
//                    basic_platform: [
//                        { required: true, message: 'Please enter basic platform name', trigger: 'blur' }
//                    ],
//                    brand: [
//                        { required: true, message: 'Please enter brand name', trigger: 'blur' }
//                    ],
//                    template: [
//                        { required: true, message: 'Please upload template file', trigger: 'blur' }
//                    ]
//                },
                optionList: [
                    {
                        label: 'ZN-7100-2DHO',
                        value: '1022'
                    },
                    {
                        label: 'AC-1027L',
                        value: '1027'
                    },
                    {
                        label: 'XN-1033',
                        value: '1033'
                    }
                ]
            };
        },
        methods: {
            //初始表单数据
            initFormData: function () {
                this.newModelForm.forEach((item, index) => {
                    if (typeof(item.key) !== 'undefined')
                        this.$set(this.modelFormData, item.key, this.newModelForm[index].value[0]);
                });
                console.log(this.modelFormData);
            },
            //初始表单校验规则
            initFormDataVaildate: function () {
                newModelForm.forEach((item) => {
                    this.modelFormData[item.key] = '';
                });
            },
            //表单提交
            formSubmit: function (vaild, formDataObject) {
//                util.ajax.request('post', this.baseuri, {}, this.formDataObject, (res) => {
//                    this.$Message.success('Are you ok!');
//                });
                let formData = new FormData();
                Object.keys(formDataObject).forEach((key) => {
                    formData.append(key, formDataObject[key]);
                });

                util.test.post('posts', formData);
                if (vaild)
                    this.$Message.success('Success !');
                else
                    this.$Message.error('Failed !');
            },
            //筛选条件发生变化
            selectChanged: function (event) {
                this.selected_model = event;
            }
        },
        created: function (){
            //this.newModelForm = newModelForm;
            this.newModelForm = parser.Json(viewData).card0;
            this.initFormData();
//            let jsonData = parser.Json(viewData);
            //console.log(jsonData);
            //console.log(this.newModelForm);
            //this.initFormDataVaildate();
        }
    }
</script>


